import React from 'react';
import styled, { keyframes } from 'styled-components';
import kh_autocomplete from '../images/kh_project/kh_autocomplete.png';
import kh_search from '../images/kh_project/kh_search.png'
import { generateMedia } from 'styled-media-query';

// 반응형 웹
const customMedia = generateMedia({
    maxmobile: '575px',
    minmobile: '352px',
});

const opacityTransition = keyframes`
    from { opacity: 0; }
    to { opacity: 0.5; }
`
const widthTransition = keyframes`
    from { width: 0%; height: 0%; }
    to { width: 50%; height: 90%; }
`

var ModalOverlay = styled.div`
`
var Projectbody = styled.div`
`
const CloseProject = styled.div`
    position: relative;
    cursor: pointer;
    top: 0.5vw;
    left: 93%;
    margin-right: 0;
    margin-bottom: 2vw;
    width: 2.5vw;
    height: 2.5vw;
    border: solid black;
    border-radius: 50%;
    opacity: 0.6;
    transform: translate(-10%);
    &:before {
        content: '';
        position: absolute;
        height: 1.5vw;
        border: solid black;
        border-width: 0 0.3vw 0.3vw 0;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(45deg);
    }
    &:after {
        content: '';
        position: absolute;
        height: 1.5vw;
        border: solid black;
        border-width: 0 0.3vw 0.3vw 0;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(-45deg);
    }
`
const Projectdetail = styled.div`
    display: flex;
    flex-direction: column;
    margin-bottom: 3vw;
    width: 100%;
`
const Title = styled.div`
    margin-left: 2vw;
    font-family: JejuGothic, NanumGothic;
    font-size: 1.5vw;
`
const Img_flex = styled.div`
    display: flex;
    flex-direction: row;
    margin-top: 2vw;
    margin-bottom: 2vw;
`
const Img_div = styled.div`
    width: 50%;
    margin-left: 2vw;
    margin-right: 2vw;
`
const Img = styled.img`
    border-radius: 1vw;
    width: 100%;
`
const Content = styled.div`
    display: flex;
    margin-right: 2vw;
    margin-left: 2vw;
    width: auto;
    font-family: JejuGothic, NanumGothic;
    font-size: 1vw;
    align-items: center;
    line-height: 2vw;
`
const state = {
    count: 0
};

const Kh_project = ({ modalOpen, modalClose }) => {
    if(state.count === 0) {
        ModalOverlay = styled.div`
            position: fixed;
            z-index: 10;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background-color: black;
            opacity: 0;
            animation: ${ opacityTransition } 0.3s forwards;
            animation-iteration-count: 1;
        `
        Projectbody = styled.div`
            position: fixed;
            z-index: 10;
            overflow: scroll;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 0%;
            height: 0%;
            border-radius: 10px;
            background-color: white;
            box-shadow: 0px 3px 6px rgba(0,0,0,0.16);
            animation: ${ widthTransition } 0.3s forwards;
            animation-iteration-count: 1;
        `
        state.count = 1;
    }

    return (
        <React.Fragment>
            {modalOpen === true ?
                <React.Fragment>
                    <ModalOverlay onClick = { modalClose }/>
                    <Projectbody>
                        <CloseProject onClick = { modalClose }/>
                        <Projectdetail>
                            <Title>1. 검색어 자동완성 기능<br></br></Title>
                            <Img_flex>
                                <Img_div>
                                    <Img src = { kh_autocomplete }/>
                                </Img_div>
                                <Img_div>
                                    <Img src = { kh_search }/>
                                </Img_div>
                            </Img_flex>
                            <Content>
                                1) 검색 버튼 누를 시 검색결과 페이지로 이동<br></br>
                                2) 검색어를 포함한 기업 리스트를 보여주고
                                선택하면 해당 기업 상세 정보 페이지로 이동
                            </Content>
                        </Projectdetail>
                        <Projectdetail>
                            <Title>2. 기업정보 게시판<br></br></Title>
                            <Img_div>
                                <Img src = { kh_autocomplete }/>
                                <Img src = { kh_search }/>
                            </Img_div>
                            <Content>
                                1) 검색 버튼 누를 시 검색결과 페이지로 이동<br></br>
                                2) 검색어를 포함한 기업 리스트를 보여주고<br></br>
                                &emsp;선택하면 해당 기업 상세 정보 페이지로 이동
                            </Content>
                        </Projectdetail>
                    </Projectbody>
                </React.Fragment>
                : ""
            }
        </React.Fragment>
    )
}

export default Kh_project;